
.article-page{
    position: absolute;
    top:0;
    left:0;
    padding:0px;
    background: #FFF;
}
.artcle-title{
    width: 2.4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-block;
    vertical-align: middle;
}
.aligning{
    vertical-align: sub;
}
.pop-header>span:nth-child(3){
    margin-left:0.13rem
}
.content-box{
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    padding-right: 20px;
    box-sizing:content-box;
}
.author-box>span{
    float:left;
}
.author-box>span:nth-child(2){margin-left:0.08rem
}
.author-box>span:nth-child(2) span{
    display: block;
}
.author-box>span:nth-child(1){
    width:0.5rem;
}
.author-box>span:nth-child(1) img{
    width:100%;
}
.author-box>span:nth-child(3){
    float:right;
    margin-top: 0.07rem;
}
.author-box{
    border:1px solid #E0DEDE;
}
.author-box{
    padding:0.08rem 0.1rem;
}
.content-inner{padding-bottom:0.66rem
}
.content-inner img{width:100%;
}
.content-inner .markdown-text p,
.content-inner .markdown-text h3,
.content-inner .markdown-text ul{
    margin-top:0.08rem;
    padding:0 0.08rem;
}
.fa-angle-left:before {
    position: relative;
    top: 1px;
}



