
.search-box .input-box {
  background: #1D8AE7;
  width: 100%;
  padding: 0.1rem 4%;
}
.search-box .input-box span {
  line-height: 0.49rem;
  width: 0.4rem;
  background: #fff;
  text-align: center;
  color: #000;
  font-size: 0.24rem;
}
.input-control {
  background: #fff;
  line-height: 0.49rem;
  margin-left: -1px;
}
.input-control > input {
  height: 0.29rem;
  width: 3rem;
  font-size: 0.2rem;
}
.mint-tab-item-label {
  font-size: 0.2rem;
}
.mint-cell-text {
  font-size: 0.18rem;
}
.search-box > .input-box > .fa-angle-left {
  font-size: 0.3rem;
}
.fa-angle-left:before {
  position: relative;
  top: -3px;
}



